import { h, Component } from 'preact';
import Textfield from '../../components/form/textfield';
import Button from '../../components/form/button';
import SearchButton from '../../components/searchButton';
import style from './style.css';
import fetchHelper from '../../functions/fetchHelper';

export default class SignIn extends Component {
	click = () => this.signIn();
	changeEmail = email => this.setState({ email });
	changePassword = password => this.setState({ password });

	signIn = () => {
		console.log(this.state);
		const url = 'https://api.cityguidr.com/users/login';
		const data = { email: this.state.email, password: this.state.password };
		console.log(data);

		fetchHelper(url, data)
			.then(data => {
				console.log(data ? JSON.parse(data) : {});
			})
			.catch(err => console.log(err));
	};

	constructor() {
		super();
		this.state = {
			email: null,
			password: null
		};
	}

	render() {
		return (
			<div class={style.content}>
				<SearchButton />
				<h1 class={style.header}>Cityguidr</h1>
				<div class={style.textfield}>
					<Textfield
						placeholder="Email"
						value={this.state.email}
						onChange={this.changeEmail}
					/>
				</div>
				<div class={style.textfield}>
					<Textfield
						type="pass"
						placeholder="Password"
						value={this.state.password}
						onChange={this.changePassword}
					/>
				</div>
				<div class={style.buttonHolder}>
					<Button title="Sign in" onClick={this.click} />
				</div>
			</div>
		);
	}
}



