import { h, Component } from 'preact';
import style from './style.css';

export default class SearchButton extends Component {
	click = () => {
		console.log('CLICK');
		this.setState({ state: !this.state.state });
	};
	constructor() {
		super();
		this.state = {
			state: false
		};
	}
	render() {
		return (
			<div class={this.state.state ? style.open : style.closed}>
				<div class={[style.border, style.red]}>test</div>
				{this.state.state && (
					<input
						class={this.state.state ? style.textfieldO : style.textfieldC}
						type="text"
						placeholder="Search for activity"
					/>
				)}

				<div
					class={this.state.state ? style.buttonO : style.buttonC}
					onClick={this.click}
				/>
			</div>
		);
	}
}



